.item {
  width: calc(100%/var(--files-items-col));
  padding: 8px 12px;
  .wrap {
    position: relative;
    cursor: pointer;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 8px;
    .panel {
      order: 1;
      position: relative;
      overflow: hidden;
      border-radius: 2px;
      background-color: #fff;
      box-shadow: 0 0 0 1px rgba(0, 0, 0, .1);
      transition: all .3s;
      width: 100%;
      .more {
        position: absolute;
        bottom: 8px;
        right: 8px;
        opacity: 0;
        transition: all .3s;
      }
      .mult {
        position: absolute;
        left: 8px;
        bottom: 8px;
        opacity: 0;
        transition: all .3s;
        :global {
          .ant-checkbox-wrapper {
            font-size: 0;
            .ant-checkbox-inner {
              width: 22px;
              height: 22px;
              border-radius: 2px;
            }
            .ant-checkbox-checked:after {
              border-radius: 2px;
            }
            .ant-checkbox .ant-checkbox-inner:after {
              inset-inline-start: 28%;
            }
          }
        }
      }
      .cover {
        position: relative;
        padding-bottom: 56.25%;
        .view {
          position: absolute;
          top: 0;
          left: 0;
          right: 0;
          bottom: 0;
        }
      }
      .info {
        padding: 8px;
        .title {
          margin-bottom: 4px;
          height: 40px;
          font-weight: 500;
          color: rgba(0,0,0,.85);
          font-size: 14px;
          line-height: 20px;
          text-overflow: ellipsis;
          overflow: hidden;
          word-break: break-all;
          display: -webkit-box;
          -webkit-box-orient: vertical;
          -webkit-line-clamp: 2;
          text-align: justify;
        }
        .attr {
          display: flex;
          justify-content: space-between;
          align-items: center;
          line-height: 22px;
          opacity: 1;
          transition: all .3s;
          .tags {
            display: flex;
          }
        }
      }
    }
    &.dir {
      margin-bottom: 0;
      &:after, &:before {
        display: block;
        content: "";
        height: 4px;
        box-shadow: 0 0 0 1px rgba(0,0,0,.1);
        border-bottom-left-radius: 2px;
        border-bottom-right-radius: 2px;
        transition: all .3s;
        background-color: #fff;
        position: relative;
      }
      &:before {
        order:3;
        width: 84%;
        z-index: -2;
      }
      &:after {
        order:2;
        width: 92%;
        z-index: -1;
      }
    }
    &:hover {
      .panel {
        box-shadow: 0 5px 16px 0 rgba(0,0,0,.15);
      }
      &.dir {
        &:after, &:before {
          box-shadow: 0 1px 16px 0 rgba(0,0,0,.3);
        }
      }
      .more {
        opacity: 1;
      }
      .mult {
        opacity: 1;
      }
      .info .attr {
        opacity: 0;
      }
    }
  }
}
